@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap');
@import 'null_style.scss';

@mixin fontst($s, $w, $c, $l_h) {
    font-size: $s;
    font-weight: $w;
    color: $c;
    line-height: $l_h;
}

@mixin button_mix ($bc, $mw, $br, $p1, $p2) {
    background-color: $bc;
    max-width: $mw;
    width: 100%;
    border-radius: $br;
    padding: $p1 $p2;
}



