@media only screen and (max-width: 768px) {
    .header__burger {
        display: block;
        width: 30px;
        height: 20px;
        position: relative;

        &:before, &:after {
            content: "";
            background-color: #FFFFFF;
            position: absolute;
            width: 100%;
            height: 2px;
            transition: all 0.3s ease 0s;
        }

        &:before {
            top: 0;
        }

        &:after {
            bottom: 0;
        }

        span {
            position: absolute;
            background-color: #FFFFFF;
            left: 0;
            width: 100%;
            height: 2px;
            top: 9px;
            transition: all 0.3s ease 0s;
        }

        &.active {
            &::before {
                transform: rotate(45deg);
                top: 9px;
            }
    
            &::after {
                transform: rotate(-45deg);
                bottom: 9px;
            }
            
            span {
