.grad {
    width: 100%;
    height: 500px;
    background-color: yellow;

    .grad__container {
        width: 100px;
        height: 100px;
        border: 1px solid black;
        margin: 0 auto;
        border-bottom: 0;
        position: relative;

        &:before {
            content: "";
            position: absolute;
            bottom: -25px;
            left: -11px;
            width: 70px;
            height: 1px;
            background-color: black;
            transform: rotate(45deg);
        }

        &:after {
            content: "";
            position: absolute;
            bottom: -25px;
            right: -11px;
            width: 70px;
            height: 1px;
            background-color: black;
            transform: rotate(-45deg);
        }
    }
}